:root {
  --background-color: #383838;
  --text-color: #fff;
  --color-white: #fff;
  --color-graphite-dark: #2f2f2f;
  --color-graphite: #383838;
  --color-text-secondary: #555;
  --color-bg-light: #f9f9f9;
  --color-bg-hover: #f1f1f1;
  --color-border: silver;
  --color-shadow: #0000000d;
  --color-accent: yellow;
  --container-max-width: 1200px;
}

body {
  color: var(--text-color);
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  z-index: 1;
  background-color: var(--color-graphite);
  box-sizing: border-box;
  background: none;
  padding: 20px 0;
  position: relative;
}

.flex {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__list {
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__logo {
  border-radius: 50%;
}

.header__burger {
  cursor: pointer;
  background: none;
  border: 0;
  margin-right: 8px;
  padding: 8px;
  display: none;
}

.header__burger-icon {
  width: 28px;
  height: 28px;
  color: var(--text-color);
  transition: transform .25s, opacity .2s;
}

.header__burger.open .header__burger-icon {
  transform: rotate(90deg)scale(1.05);
}

@media (width <= 768px) {
  .header__burger {
    display: inline-flex;
  }

  .header__nav {
    z-index: 1200;
    justify-content: flex-start;
    align-items: flex-start;
    display: none;
    position: fixed;
  }

  .header__nav.open {
    display: flex;
  }

  .header__nav .header__panel {
    width: 100%;
    height: 100%;
    color: var(--text-color);
    box-shadow: none;
    backdrop-filter: none;
    z-index: 1250;
    background: #0c0c0cf0;
    border-radius: 0;
    flex-direction: column;
    gap: 16px;
    padding: 56px 18px 18px;
    display: flex;
    top: 80px;
  }

  .header__nav .header__list {
    flex-direction: column;
    align-items: center;
    gap: 16px;
    margin: 0;
    padding: 0;
    display: flex;
  }

  .header__nav .header__list .header__link {
    color: var(--text-color);
    text-align: center;
    padding: 8px 6px;
    font-size: 1.05rem;
  }

  .header {
    position: relative;
  }
}

.mobile-modal {
  display: none;
}

.mobile-modal.open {
  display: block;
}

.mobile-modal__backdrop {
  z-index: 1200;
  background: #0000008f;
  position: fixed;
  inset: 0;
}

.mobile-modal__content {
  width: 92%;
  max-width: 440px;
  color: var(--text-color);
  z-index: 1250;
  background: #0c0c0ce0;
  border-radius: 12px;
  flex-direction: column;
  gap: 12px;
  padding: 20px 18px 26px;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 18px 50px #0009;
}

.mobile-modal__close {
  color: var(--text-color);
  cursor: pointer;
  background: none;
  border: 0;
  font-size: 24px;
  line-height: 1;
  position: absolute;
  top: 10px;
  right: 10px;
}

.mobile-modal__nav .header__list {
  flex-direction: column;
  align-items: center;
  gap: 12px;
  margin: 16px 0 0;
  display: flex;
}

.mobile-modal__nav .header__link {
  text-align: center;
  width: 100%;
  padding: 8px 6px;
  font-size: 1.05rem;
  display: block;
}

.mobile-modal__nav .header__list {
  align-items: stretch;
  width: 100%;
  max-width: 420px;
  margin: 16px auto 0;
}

.mobile-modal__nav .header__list .header__item {
  width: 100%;
}

.mobile-modal__nav .header__list .header__link {
  width: 100%;
  padding: 12px 14px;
  display: block;
}

@media (width >= 769px) {
  .mobile-modal {
    display: none !important;
  }

  .header__panel-close, .mobile-modal__close {
    visibility: hidden;
    display: none !important;
  }
}

.header__panel-close {
  color: var(--text-color);
  cursor: pointer;
  background: none;
  border: 0;
  justify-content: center;
  align-items: center;
  padding: 6px;
  display: inline-flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.header__panel {
  position: relative;
}

.header__panel-close svg {
  opacity: .95;
}

.header__panel-close:hover {
  background: #ffffff0a;
  border-radius: 8px;
}

.header__panel-close:focus {
  border-radius: 8px;
  outline: 2px solid #ffffff1f;
}

.panel--hidden {
  opacity: 0;
  display: none !important;
  transform: none !important;
}

.header__overlay-close {
  z-index: 1260;
  color: var(--text-color);
  cursor: pointer;
  background: #ffffff0a;
  border: 0;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  display: inline-flex;
  position: fixed;
  top: 18px;
  right: 18px;
  box-shadow: 0 6px 18px #00000073;
}

.header__overlay-close:hover {
  background: #ffffff0f;
}

.header__overlay-close svg {
  opacity: .95;
}

.burger__bar {
  background: var(--text-color);
  width: 22px;
  height: 2px;
  margin: 4px 0;
  transition: transform .25s, opacity .2s;
  display: block;
}

.header__nav {
  display: block;
}

@media (width <= 768px) {
  .header__list {
    flex-direction: column;
    gap: 12px;
    padding: 16px;
    display: none;
  }

  .header__burger {
    display: inline-flex;
  }

  .header__nav.open .header__list {
    z-index: 1000;
    background: #0000000a;
    border-radius: 8px;
    min-width: 160px;
    display: flex;
    position: absolute;
    top: 100%;
    right: 16px;
    box-shadow: 0 8px 20px #0000001f;
  }

  .header {
    position: relative;
  }
}

.header__link {
  color: var(--text-color);
  padding-bottom: 4px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
  position: relative;
}

.header__link:hover {
  color: var(--text-color) !important;
}

.header__link:after {
  content: "";
  transform-origin: 100%;
  background-color: #ff0;
  width: 100%;
  height: 2px;
  transition: transform .3s;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.header__link:hover:after {
  transform-origin: 0;
  transform: scaleX(1);
}

.work__list {
  color: #fff;
  text-align: left;
}

.hero {
  color: #fff;
  background-image: url("background.60ce8b9d.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: var(--background-color);
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin-top: -200px;
  padding-top: 100px;
  padding-bottom: 100px;
  display: flex;
}

.hero__description {
  max-width: 600px;
}

.hero__logo {
  border-radius: 20px;
}

.hero__text {
  background-color: var(--background-color);
  border-radius: 10px;
  outline: 2px solid #ff0;
  margin-left: auto;
  padding: 10px 20px;
}

.article {
  background-color: var(--color-white);
  box-shadow: 0 4px 20px var(--color-shadow);
  border-radius: 16px;
  padding: 40px;
}

.article__title {
  color: var(--color-graphite-dark);
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;
  font-weight: 700;
}

.to-top {
  z-index: 1000;
  backdrop-filter: blur(6px);
  opacity: 0;
  pointer-events: none;
  background: linear-gradient(#ffffff0f, #ffffff05);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 64px;
  height: 64px;
  transition: transform .35s cubic-bezier(.2, .9, .3, 1), opacity .35s, box-shadow .2s;
  display: inline-flex;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transform: translateY(40px)scale(.95);
  box-shadow: 0 8px 24px #00000059;
}

.to-top img {
  width: 100%;
  height: 100%;
  display: block;
}

.to-top.show {
  opacity: 1;
  pointer-events: auto;
  transform: translateY(0)scale(1);
}

.to-top:hover {
  transform: translateY(-4px)scale(1.03);
  box-shadow: 0 12px 30px #00000073;
}

.to-top img {
  object-fit: contain;
  width: 48px;
  height: 48px;
}

.article__description {
  text-align: center;
  color: var(--color-text-secondary);
  margin-bottom: 40px;
  font-size: 1.1rem;
}

.footer {
  background-color: var(--background-color);
  padding-top: 50px;
  padding-bottom: 50px;
}

.footer__flex-elements {
  display: flex;
}

.footer__list {
  justify-content: center;
  align-items: end;
  gap: 30px;
  margin-left: auto;
  list-style: none;
  display: flex;
}

.footer__link {
  color: var(--text-color);
  padding-bottom: 4px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
  position: relative;
}

@media (width <= 600px) {
  .footer__list {
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-left: 0;
    padding: 12px 0;
  }

  .footer__flex-elements {
    flex-direction: column;
    align-items: center;
    gap: 12px;
  }
}

.price__img {
  width: 520px;
  max-width: 100%;
  height: auto;
  margin: 0 auto;
  display: block;
}

@media (width <= 1200px) {
  .price__img {
    width: 420px;
  }
}

@media (width <= 768px) {
  .price__img {
    width: 320px;
  }
}

.to-top.in-section {
  position: fixed;
  bottom: 16px;
  right: 16px;
}

.footer__link:after {
  content: "";
  transform-origin: 100%;
  background-color: #ff0;
  width: 100%;
  height: 2px;
  transition: transform .3s;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.footer__link:hover:after {
  transform-origin: 0;
  transform: scaleX(1);
}

.footer__link:hover {
  color: var(--text-color) !important;
}

.footer__logo {
  border-radius: 20px;
  width: 80px;
  height: 80px;
}

.footer__text {
  text-align: center;
}

.footer__text:before {
  content: "";
  background-color: var(--text-color);
  opacity: .2;
  box-sizing: border-box;
  border-radius: 10px;
  width: 100%;
  height: 1px;
  margin: 20px auto;
  display: block;
}

.article__list-title, .recomandation__title, .prohibitions__title, .duty__title, .membership__title {
  color: var(--color-graphite-dark);
  border-left: 4px solid var(--color-accent);
  margin: 30px 0 15px;
  padding-left: 10px;
  font-size: 1.4rem;
  font-weight: 700;
}

.membership__description {
  color: var(--color-text-secondary);
  margin-bottom: 10px;
  font-style: italic;
}

.article__list, .recomandation__list, .prohibitions__list, .duty__list, .membership__list {
  margin-bottom: 40px;
  padding-left: 20px;
  list-style: inside decimal;
}

.article__item, .recomandation__item, .prohibitions__item, .duty__item, .membership__item {
  background-color: var(--color-bg-light);
  border-left: 4px solid var(--color-border);
  color: var(--background-color);
  border-radius: 8px;
  margin-bottom: 10px;
  padding: 15px 20px;
  transition: all .3s;
}

.article__item:hover, .recomandation__item:hover, .prohibitions__item:hover, .duty__item:hover, .membership__item:hover {
  background-color: var(--color-bg-hover);
  border-left-color: var(--color-accent);
}

.artcle__item-text, .recomandation__text, .prohibitions__text, .duty__text, .membership__text {
  color: var(--color-graphite);
  font-size: 1rem;
  font-weight: 400;
}

.info__text, .info__text2, .info__text3 {
  color: var(--color-graphite-dark);
  background-color: var(--color-bg-light);
  border-left: 4px solid var(--color-accent);
  border-radius: 8px;
  margin-top: 20px;
  padding: 15px 20px;
  font-size: 1rem;
}

@media (width <= 768px) {
  .article {
    padding: 25px;
  }

  .article__title {
    font-size: 1.6rem;
  }

